'use client';

import { useEffect, useState } from 'react';
import { Input } from '@/src/components/ui/input';
import { Button } from '@/src/components/ui/button';
import { SimpleSelect } from '@/src/components/ui/select';
import { Staff } from '@/src/lib/staff/staffTypes';
import { createStaff, updateStaff } from '@/src/lib/services/staff/staffService';
import { inviteUserAsStaff } from '@/src/lib/invitations/inviteUserAsStaff';
import NotificationDialog from '@/src/components/ui/NotificationDialog';
import { collection, getDocs, query, where } from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';

interface Props {
  editingStaff: Staff | null;
  companyId: string;
  onSave: () => void;
  onCancel: () => void;
}

const ROLE_OPTIONS = [
  { label: 'Company Admin', value: 'company_admin' },
  { label: 'Station Manager', value: 'station_manager' },
  { label: 'Technician', value: 'technician' },
  { label: 'Support', value: 'support' },
];

export default function StaffForm({ editingStaff, companyId, onSave, onCancel }: Props) {
  const [form, setForm] = useState<Omit<Staff, 'id' | 'createdAt' | 'updatedAt'>>({
    userId: '',
    companyId,
    stationId: '',
    role: 'support',
    name: '',
    email: '',
    phone: '',
  });

  const [dialog, setDialog] = useState({
    open: false,
    type: 'info' as 'info' | 'success' | 'error',
    title: '',
    description: '',
  });

  const [searchEmail, setSearchEmail] = useState('');
  const [stationOptions, setStationOptions] = useState<{ label: string; value: string }[]>([]);

  useEffect(() => {
    if (editingStaff) {
      const { id, createdAt, updatedAt, ...rest } = editingStaff;
      setForm(rest);
    }
  }, [editingStaff]);

  useEffect(() => {
    const fetchStations = async () => {
      const snap = await getDocs(query(collection(db, 'rentalStations'), where('companyId', '==', companyId)));
      const options = snap.docs.map((doc) => ({ label: doc.data().name, value: doc.id }));
      setStationOptions(options);
    };
    if (companyId) fetchStations();
  }, [companyId]);

  const handleChange = (key: keyof typeof form, value: string) => {
    setForm((prev) => ({ ...prev, [key]: value }));
  };

  const handleEmailSearch = async () => {
    if (!searchEmail) return;
    try {
      const snapshot = await getDocs(query(collection(db, 'users'), where('email', '==', searchEmail)));
      if (snapshot.empty) {
        setDialog({ open: true, type: 'error', title: 'User not found', description: 'No user with this email exists.' });
        return;
      }
      const userDoc = snapshot.docs[0];
      const user = userDoc.data();
      setForm((prev) => ({
        ...prev,
        userId: userDoc.id,
        name: user.name || '',
        email: user.email || '',
        phone: user.phone || '',
      }));
      setDialog({ open: true, type: 'success', title: 'User Loaded', description: '' });
    } catch (error: any) {
      setDialog({ open: true, type: 'error', title: 'Error', description: error.message || 'Failed to search user.' });
    }
  };

  const handleSubmit = async () => {
    if (!form.userId || !form.name || !form.email) {
      setDialog({ open: true, type: 'error', title: 'Missing Required Fields', description: 'User ID, Name, and Email are required.' });
      return;
    }
    try {
      if (editingStaff) {
        await updateStaff(editingStaff.id, form);
        setDialog({ open: true, type: 'success', title: 'Staff Updated Successfully', description: '' });
      } else {
        await inviteUserAsStaff({
          email: form.email,
          companyId,
          stationId: form.stationId || undefined,
          role: form.role,
          message: `${form.name} is invited to join your company as ${form.role}`,
        });
        setDialog({ open: true, type: 'success', title: 'Invitation Sent Successfully', description: '' });
      }
      onSave();
    } catch (error: any) {
      setDialog({ open: true, type: 'error', title: 'Error', description: error.message || 'Failed to save staff.' });
    }
  };

  return (
    <div className="bg-white p-6 rounded-xl shadow max-w-3xl mx-auto space-y-4">
      <h2 className="text-xl font-semibold text-gray-800">
        {editingStaff ? 'Edit Staff' : 'Invite New Staff'}
      </h2>
      <div className="flex gap-2">
        <Input placeholder="Search by Email" value={searchEmail} onChange={(e) => setSearchEmail(e.target.value)} />
        <Button onClick={handleEmailSearch}>Find</Button>
      </div>
      <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
        <Input placeholder="Full Name" value={form.name} onChange={(e) => handleChange('name', e.target.value)} />
        <Input placeholder="Email" value={form.email} onChange={(e) => handleChange('email', e.target.value)} />
        <Input placeholder="Phone" value={form.phone} onChange={(e) => handleChange('phone', e.target.value)} />
        <SimpleSelect
          options={stationOptions}
          value={form.stationId ?? ''}
          onChange={(val) => handleChange('stationId', val)}
          placeholder="Select Station (optional)"
        />
        <div className="sm:col-span-2">
          <SimpleSelect
            options={ROLE_OPTIONS}
            value={form.role}
            onChange={(val) => handleChange('role', val)}
            placeholder="Select Role"
          />
        </div>
      </div>
      <div className="flex justify-end gap-2 pt-4">
        <Button onClick={handleSubmit}>Save</Button>
        <Button variant="ghost" onClick={onCancel}>Cancel</Button>
      </div>

      <NotificationDialog
        open={dialog.open}
        type={dialog.type}
        title={dialog.title}
        description={dialog.description}
        onClose={() => setDialog((prev) => ({ ...prev, open: false }))}
      />
    </div>
  );
}