'use client';

import { Staff } from '@/src/lib/staff/staffTypes';
import { Button } from '@/src/components/ui/button';

interface Props {
  staffs: Staff[];
  onEdit: (staff: Staff) => void;
  onDelete: (id: string) => void;
  companyName: string;
  stationMap: Record<string, string>;
}

export default function StaffTable({ staffs, onEdit, onDelete, companyName, stationMap }: Props) {
  return (
    <div className="overflow-x-auto bg-white shadow rounded-xl p-4">
      <table className="min-w-full border border-gray-200 text-sm whitespace-nowrap">
        <thead className="bg-gray-100 text-gray-700 text-sm font-medium">
          <tr>
            <th className="px-3 py-2 border text-left">Name</th>
            <th className="px-3 py-2 border text-left">Email</th>
            <th className="px-3 py-2 border text-left">Phone</th>
            <th className="px-3 py-2 border text-left">Role</th>
            <th className="px-3 py-2 border text-left">Company</th>
            <th className="px-3 py-2 border text-left">Station</th>
            <th className="px-3 py-2 border text-center">Status</th>
            <th className="px-3 py-2 border text-center">Actions</th>
          </tr>
        </thead>
        <tbody>
          {staffs.map((staff) => (
            <tr key={staff.id} className="hover:bg-gray-50">
              <td className="px-3 py-2 border">{staff.name}</td>
              <td className="px-3 py-2 border">{staff.email}</td>
              <td className="px-3 py-2 border">{staff.phone}</td>
              <td className="px-3 py-2 border capitalize">{staff.role.replace('_', ' ')}</td>
              <td className="px-3 py-2 border">{companyName}</td>
              <td className="px-3 py-2 border">{staff.stationId ? stationMap[staff.stationId] || staff.stationId : '-'}</td>
              <td className="px-3 py-2 border text-center">
                {staff.accepted === true ? (
                  <span className="text-green-600 font-medium">Accepted</span>
                ) : staff.accepted === false ? (
                  <span className="text-yellow-600 font-medium">Pending</span>
                ) : (
                  <span className="text-gray-500">Unknown</span>
                )}
              </td>
              <td className="px-3 py-2 border text-center space-x-2">
                <Button  onClick={() => onEdit(staff)}>Edit</Button>
                <Button  variant="destructive" onClick={() => onDelete(staff.id)}>Delete</Button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
