// lib/staff/inviteUserAsStaff.ts
import { collection, addDoc, getDocs, query, where, Timestamp, serverTimestamp } from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';

export async function inviteUserAsStaff({
  email,
  companyId,
  stationId,
  role,
  message,
}: {
  email: string;
  companyId: string;
  stationId?: string;
  role: 'support' | 'technician' | 'station_manager' | 'company_admin';
  message?: string;
}) {
  // T√¨m userId t·ª´ email
  const q = query(collection(db, 'users'), where('email', '==', email));
  const snap = await getDocs(q);

  if (snap.empty) {
    throw new Error('Kh√¥ng t√¨m th·∫•y ng∆∞·ªùi d√πng v·ªõi email n√†y.');
  }

  const userDoc = snap.docs[0];
  const userId = userDoc.id;
  const userData = userDoc.data();

  // G·ª≠i l·ªùi m·ªùi v√†o messages
  const invitation = {
    userId,
    type: 'invitation',
    role,
    companyId,
    stationId: stationId || null,
    status: 'pending',
    content: message || `You are invited to join the company as ${role}`,
    createdAt: Timestamp.now(),
  };

  await addDoc(collection(db, 'messages'), invitation);

  // T·∫°o lu√¥n staff document v·ªõi accepted: false
  await addDoc(collection(db, 'staffs'), {
    userId,
    companyId,
    stationId: stationId || null,
    role,
    name: userData.name || '',
    email: userData.email || email,
    phone: userData.phone || '',
    accepted: false,
    createdAt: serverTimestamp(),
    updatedAt: serverTimestamp(),
  });
}
