import { useEffect, useState } from 'react';
import { Staff } from '@/src/lib/staff/staffTypes';
import {
  createStaff,
  updateStaff,
  deleteStaff,
} from '@/src/lib/services/staff/staffService';
import { db } from '@/src/firebaseConfig';
import {
  collection,
  onSnapshot,
  query,
  where
} from 'firebase/firestore';

export function useStaffData(companyId: string) {
  const [staffs, setStaffs] = useState<Staff[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (!companyId) return;

    const q = query(collection(db, 'staffs'), where('companyId', '==', companyId));
    const unsubscribe = onSnapshot(q, (snapshot) => {
      const updatedStaffs: Staff[] = snapshot.docs.map((doc) => ({
        ...(doc.data() as Staff),
        id: doc.id,
      }));
      setStaffs(updatedStaffs);
      setLoading(false);
    }, (error) => {
      console.error('Realtime fetch staff error:', error);
    });

    return () => unsubscribe();
  }, [companyId]);

  const handleCreate = async (staff: Omit<Staff, 'id' | 'createdAt' | 'updatedAt'>) => {
    try {
      await createStaff({ ...staff, accepted: false });
    } catch (error) {
      console.error('Failed to create staff:', error);
    }
  };

  const handleUpdate = async (id: string, data: Partial<Staff>) => {
    try {
      await updateStaff(id, data);
    } catch (error) {
      console.error('Failed to update staff:', error);
    }
  };

  const handleDelete = async (id: string) => {
    try {
      await deleteStaff(id);
    } catch (error) {
      console.error('Failed to delete staff:', error);
    }
  };

  return {
    staffs,
    loading,
    handleCreate,
    handleUpdate,
    handleDelete,
  };
}
